.i{
    display: flex;
    height: 100vh;
  }
  .i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .i-right{
    flex: 1;
    position: relative;
  }
  .i-left-wrapper{
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .i-intro{
    font-size: 30px;
    font-weight: 300;
  }
  .i-name{
    font-size: 60px;
  }
  .i-title{
    height: 50px;
    overflow: hidden;
  }
  .i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
  }
  
  @keyframes move {
     25%{
      transform: translateY(-50px);
     }
     50%{
      transform: translateY(-100px);
     }
     75%{
      transform: translateY(-150px);
     }
     100%{
      transform: translateY(-200px);
     }
  }
  
  .i-title-items{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: green;
    display: flex;
    align-items: center;
  
  }
.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;

}
.i-bg{
    clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color:#59b256 ;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}


/*=======CTA==========*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}
.btn.btn_cv:hover{
    color:var(--color-bg) ;
}



/* ==========MEDIA=============== */

@media screen and (max-width:1024px) {
 
    header{
        height: 68vh;
    }
    .i{
        flex-direction: column;
    }
    .i-left-wrapper{
      align-items: center;
      text-align: center;
      height: 100%;
      
    }
    .i-img{
      object-fit: cover;
      height: 100%;
    }
}
@media screen and (max-width:600px) {
    .i{
        flex-direction: column;
    }
    .i-left-wrapper{
      padding: 10px;
    align-items: center;
    text-align: center;
    height: 100%;
    }
    .i-desc{
      display: none;
    }
    
   header{
    height: 100vh;
   }
    .header_social, .scroll_down {
        display: none;
    }
}